import React, { Component } from 'react';

class Nav extends Component {
	splashFilter = () => {
		this.props.showSplash();
		this.props.resetFilter();
	}

	activeNav = (activepage) => {
		if (activepage) {
			return "nav-item active"
		} else {
			return "nav-item"
		}
	}
	render() {
		return (
			<nav className="navbar fixed-top navbar-expand-sm navbar-dark ">
			  <span className="navbar-brand-custom">ElectricDriver.net</span>
			  <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
			    <span className="navbar-toggler-icon"></span>
			  </button>
			  <div className="collapse navbar-collapse" id="navbarSupportedContent">
			    <ul className="navbar-nav mr-auto">

					<li className={this.activeNav(this.props.show.Splash)}>
							<span className="nav-link"
							onClick={
								this.splashFilter
							}>
								Search
							</span>
						</li>
						<li className={this.activeNav(this.props.show.Learn)}>
							<span className="nav-link"
							onClick={
								this.props.showLearn
							}>
								Learn
							</span>
						</li>
						<li className={this.activeNav(this.props.show.Explore)}>
							<span className="nav-link"
							onClick={
								this.props.showExplore
							}>
								Explore
							</span>
						</li>
			    </ul>
					<ul className="navbar-nav ml-auto">
						<li className={this.activeNav(this.props.show.About)}>
							<span className="nav-link"
							onClick={
								this.props.showAbout
							}>
								About
							</span>
						</li>
					</ul>
			  </div>
			</nav>
		);
	}
}

export default Nav;
