import React, { Component } from 'react';

class Splash extends Component {

	state = {
		makes: ['Any Make', 'BMW', 'Chevrolet', 'Ford','Honda', 'Hyundai', 'Nissan', 'Tesla', 'Toyota']
	};

	showMe = () => {
		if (this.props.show) {
			return "Splash"
		} else {
			return "Splash hidden"
		}
	}
	render() {
		return (
			<div className={this.showMe()}>
				<div className="row row-spacer"></div>
				<div className="row">
					<div className="col-2 col-lg-4"></div>
					<div className="splash-text center-text col-8 col-lg-4">
						<p>
							Search and compare the most popular <span className="bold italic">EV</span> and <span className="bold italic">PHEV</span> models on the market!
						</p>
					</div>
					<div className="col-2"></div>
				</div>
				<div id="search" className="row">
					<div  className="col-md-2 col-lg-4"></div>
					<div className="col-md-5 col-lg-2">
						<select id="make"
						onChange={event => this.props.setMake(event.target.value)}
						className="form-control" value={this.props.parameters.make}>
							<option>Any Make</option>
							<option>BMW</option>
							<option>Chevrolet</option>
							<option>Ford</option>
							<option>Honda</option>
							<option>Hyundai</option>
							<option>Nissan</option>
							<option>Tesla</option>
							<option>Toyota</option>
						</select>
					</div>
					<div className="col-md-3 col-lg-2">
						<button onClick={this.props.showResults} className="btn btn-primary form-control" id="compare-btn">Compare!</button>
					</div>
				</div>
			</div>
		);
	}
}

export default Splash;
