import React, { Component } from 'react';
import Nav from './components/Nav';
import Splash from './components/Splash';
import Headlines from './components/Headlines';
import Results from './components/Results';
import Learn from './components/Learn';
import Explore from './components/Explore';
import About from './components/About';



class App extends Component {
	state = {
		show: {
			Splash: true,
			Headlines: true,
			Results: false,
			Learn: false,
			Explore: false,
			About: false,
			MobileFilter: false
		},
		parameters: {
			make: 'Any Make',
			minprice: 0,
			maxprice: 100000,
			minrange: 0,
			maxrange: 500,
		}

	}

	setMinprice = (nminprice) => {
		var parameters = {...this.state.parameters}
		parameters.minprice = nminprice;
		this.state.show.MobileFilter = false;
		this.setState({parameters});
	}
	setMaxprice = (nmaxprice) => {
		var parameters = {...this.state.parameters}
		parameters.maxprice = nmaxprice;
		this.state.show.MobileFilter = false;
		this.setState({parameters});
	}
	setMinrange = (nminrange) => {
		var parameters = {...this.state.parameters}
		parameters.minrange = nminrange;
		this.state.show.MobileFilter = false;
		this.setState({parameters});
	}
	setMake = (make) => {
		var parameters = {...this.state.parameters}
		parameters.make = make;
		this.state.show.MobileFilter = false;
		this.setState({parameters});
	}

	toggleFilter = () => {
		var show = {...this.state.show}
		show.MobileFilter = !show.MobileFilter;
		this.setState({show});
	}

	showResults = () => {
		this.setState({  show : {Splash: false, Headlines: false, Results: true, Learn: false, Explore: false, About: false }  });
	}

	showSplash = () => {
		this.setState({  show : {Splash: true, Headlines: true, Results: false, Learn: false, Explore: false, About: false }  });
	}

	showLearn = () => {
		this.setState({  show : {Splash: false, Headlines: false, Results: false, Learn: true, Explore: false, About: false }  });
	}

	showExplore = () => {
		this.setState({  show : {Splash: false, Headlines: false, Results: false, Learn: false, Explore: true, About: false }  });
	}

	showAbout = () => {
		this.setState({  show : {Splash: false, Headlines: false, Results: false, Learn: false, Explore: false, About: true }  });
	}

	showFilter = () => {
		this.setState({  show : {Splash: false, Headlines: false, Results: true, Learn: false, Explore: false, About: true }  });
	}

	resetFilter = () => {
		var parameters = {...this.state.parameters}
		parameters.make='Any Make';
		parameters.minprice=0;
		parameters.maxprice=100000;
		parameters.minrange=0;
		parameters.maxrange=500;
		this.setState({parameters});
	}

	render() {
		return (
			<div className="App">
				<Nav
					show={this.state.show}
					showSplash={this.showSplash}
					showLearn={this.showLearn}
					showExplore={this.showExplore}
					showAbout={this.showAbout}
					resetFilter={this.resetFilter}
				/>
				<Splash
					setMake={this.setMake}
					show={this.state.show.Splash}
					showResults={this.showResults}
					parameters={this.state.parameters}
				/>
				<Headlines
					show={this.state.show.Headlines}
				/>
				<Results
					show={this.state.show.Results}
					showMobileFilter={this.state.show.MobileFilter}
					toggleFilter={this.toggleFilter}
					parameters={this.state.parameters}
					setMake={this.setMake}
					setMinprice={this.setMinprice}
					setMaxprice={this.setMaxprice}
					setMinrange={this.setMinrange}
					resetFilter={this.resetFilter}
				/>
				<Learn
					show={this.state.show.Learn}
				/>
				<Explore
					show={this.state.show.Explore}
				/>
				<About
					show={this.state.show.About}
				/>
			</div>
		);
	}
}

export default App;
