import React, { Component } from 'react';
import Filter from './Filter';

import boltthumb from '../thumbnails/bolt.jpg';
import claritythumb from '../thumbnails/clarity.jpg';
import cybertruckthumb from '../thumbnails/cybertruck.jpg';
import fusionthumb from '../thumbnails/fusion.jpg';
import i3thumb from '../thumbnails/i3.jpg';
import ioniqthumb from '../thumbnails/ioniq.jpg';
import konathumb from '../thumbnails/kona.jpg';
import leafthumb from '../thumbnails/leaf.jpg';
import machethumb from '../thumbnails/mach-e.jpg';
import model3thumb from '../thumbnails/model3.jpg';
import modelsthumb from '../thumbnails/models.jpg';
import modelxthumb from '../thumbnails/modelx.jpg';
import modelythumb from '../thumbnails/modely.jpg';
import niroevthumb from '../thumbnails/niroev.jpg';
import nirophevthumb from '../thumbnails/nirophev.jpg';
import priusprimethumb from '../thumbnails/priusprime.jpg';

class Results extends Component {

	showMe = () => {
		if (this.props.show) {
			return "Results row"
		} else {
			return "Results hidden"
		}
	}

	vehicles = [
		{year: 2020, make: 'BMW', model: 'i3', msrp: 44450, tax: 7500, range: 153, kbb: 4.5, body: 'Compact Sedan', drive: 'RWD', type: 'EV', img: i3thumb},
		{year: 2020, make: 'Chevrolet', model: 'Bolt', msrp: 37495, tax: 1875, range: 259, kbb: 4.7, body: 'Compact Sedan', drive: 'FWD', type: 'EV', img: boltthumb},
		{year: 2021, make: 'Ford', model: 'Mustang Mach-E', msrp: 43895, tax: 7500, range: 230, kbb: null, body: 'Hatchback', drive: 'RWD', type: 'EV', img: machethumb},
		{year: 2020, make: 'Ford', model: 'Fusion PHEV', msrp: 35000, tax: 4609, range: 26, kbb: null, body: 'Sedan', drive: 'FWD', type: 'PHEV', img: fusionthumb},
		{year: 2020, make: 'Honda', model: 'Clarity', msrp: 33400, tax: 7500, range: 89, kbb: 4.7, body: 'Sedan', drive: 'FWD', type: 'PHEV', img: claritythumb},
		{year: 2020, make: 'Hyundai', model: 'Kona', msrp: 37190, tax: 7500, range: 258, kbb: 4.5, body: 'SUV', drive: 'FWD', type: 'EV', img: konathumb},
		{year: 2020, make: 'Hyundai', model: 'Ioniq PHEV', msrp: 26500, tax: 4543, range: 29, kbb: 4.4, body: 'Compact Sedan', drive: 'FWD', type: 'PHEV', img: ioniqthumb},
		{year: 2020, make: 'Hyundai', model: 'Ioniq Electric', msrp: 33045, tax: 7500, range: 170, kbb: 4.4, body: 'Compact Sedan', drive: 'FWD', type: 'EV', img: ioniqthumb},
		{year: 2020, make: 'Nissan', model: 'Leaf', msrp: 31600, tax: 7500, range: 149, kbb: 4.4, body: 'Compact Sedan', drive: 'FWD', type: 'EV', img: leafthumb},
		{year: 2020, make: 'Tesla', model: 'Model 3', msrp: 39990, tax: 0, range: 250, kbb: 4.7, body: 'Sedan', drive: 'RWD', type: 'EV', img: model3thumb},
		{year: 2020, make: 'Tesla', model: 'Model S', msrp: 79990, tax: 0, range: 390, kbb: 4.9, body: 'Sedan', drive: 'AWD', type: 'EV', img: modelsthumb},
		{year: 2020, make: 'Tesla', model: 'Model X', msrp: 84990, tax: 0, range: 351, kbb: 4.8, body: 'SUV', drive: 'AWD', type: 'EV', img: modelxthumb},
		{year: 2020, make: 'Tesla', model: 'Model Y', msrp: 52990, tax: 0, range: 315, kbb: null, body: 'Compact SUV', drive: 'AWD', type: 'EV', img: modelythumb},
		{year: 2021, make: 'Tesla', model: 'Cybertruck', msrp: 39990, tax: 0, range: 250, kbb: null, body: 'Pickup', drive: 'AWD', type: 'EV', img: cybertruckthumb},
		{year: 2020, make: 'Toyota', model: 'Prius Prime', msrp: 27750, tax: 4502, range: 25, kbb: 4.7, body: 'Sedan', drive: 'FWD', type: 'PHEV', img: priusprimethumb},
		{year: 2020, make: 'Kia', model: 'Niro PHEV', msrp: 29545, tax: 4543, range: 26, kbb: 4.5, body: 'SUV', drive: 'FWD', type: 'PHEV', img: nirophevthumb},
		{year: 2019, make: 'Kia', model: 'Niro EV', msrp: 38500, tax: 7500, range: 239, kbb: null, body: 'SUV', drive: 'FWD', type: 'EV', img: niroevthumb}
	]

	render() {
		return (
			<div className={this.showMe()}>

				<div className="col-lg-2"></div>
				<button id="show-filter" className="btn btn-primary form-control"
				onClick={this.props.toggleFilter}
				>
					Show Filter
				</button>
				<Filter
					showMobile={this.props.showMobileFilter}
					toggleFilter={this.props.toggleFilter}
					parameters={this.props.parameters}
					setMake={this.props.setMake}
					setMinprice={this.props.setMinprice}
					setMaxprice={this.props.setMaxprice}
					setMinrange={this.props.setMinrange}
					resetFilter={this.props.resetFilter}
				/>

				<div className="col-md-9 col-lg-8">
							{
								this.vehicles.map((vehicle, index) => {
									//filter make
									if(vehicle.make === this.props.parameters.make || 'Any Make' === this.props.parameters.make){
									//filter min price
									if(vehicle.msrp > this.props.parameters.minprice){
									//filter max price
									if(vehicle.msrp < this.props.parameters.maxprice){
									//filter minrange
									if(vehicle.range > this.props.parameters.minrange){
									return(
										<div className="row car-result" key={index}>

											<div className="car-thumb col-4 col-sm-6">
												<img src={vehicle.img} alt={vehicle.make + vehicle.model} />
											</div>
											<div className="car-info col-8 col-sm-6">
												<span className="car-name"><span className="car-year">{vehicle.year}&nbsp;</span>{vehicle.make}&nbsp;<span className="car-model">{vehicle.model}</span></span>
												<br />
												<span className="car-price">Base Price: ${vehicle.msrp}</span>
												<br />
												<span className="car-tax">Federal Tax Credit: ${vehicle.tax}</span>
												<br />
												<span className="car-range">All-electric range: {vehicle.range} mi</span>
												<br />
												<span className="kbb">KBB Expert Rating: {vehicle.kbb}</span>
												<br />
												<span className="car-desc">{vehicle.body} | {vehicle.drive} | &nbsp;<span className={vehicle.type}>{vehicle.type}</span></span>
											</div>
										</div>
									);

									}}}} else {return null}
								})
							}
						</div>
					</div>
		);
	}
}

export default Results;
